* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open sans", Helvetica, Arial;
}

:root {
  --theme-blue: #00AEEF;
}

p {
  font-size: 16px;
  font-family: 'Raleway', sans-serif;
  /* text-align: justify; */

}

a {
  text-decoration: none;
  color: black;
}
a:hover {
  color: rgb(2, 91, 127);
}

.main-container {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  /* margin: 10px; */
  background-color: #f8f9fa;
}
.emptyList-wrap {
  display: flex;
  justify-content: center;
}
.emptyList-wrap img {
  max-width: 500px;
  width: 100%;
}

.grid-2{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}
.grid-4{
  /* display: flex; */
  /* margin-top: 20px; */
  /* width: 100%; */
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  /* margin: 2rem; */

}
.grid-1-3 {
  display: grid;
  grid-template-columns: 25% auto;
  grid-gap: 3rem;
}
.page-goback{
  margin: 20px 0px;
}
.page-goback a{
  color: black;
}
.page-goback a:hover{
  color: rgb(0, 78, 122);
  
}

.section {
  margin: 10px 100px;
}

.section-heading h1{
  color: var(--theme-blue);
  font-size: 44px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 900;
  /* text-align: center; */
  font-family: "Open sans", Helvetica, Arial;
}
.para-content{
  margin-bottom: 20px;
}

.para-content p{
  font-size: 20px;
  font-family: 'Raleway', sans-serif;
}
.para-content a{
  text-decoration: none;
  color: black;
  font-size: 20px;
  font-family: 'Raleway', sans-serif;
}
.para-content a:hover{
  color: rgb(1, 68, 113);
}
.para-content ol li{
  font-size: 20px;
  font-family: 'Raleway', sans-serif;
}

.list-items{
  text-decoration: none;
  color: black;
  font-size: 20px;
  font-family: 'Raleway', sans-serif;
}
.list-items a{
  text-decoration: none;
  color: black;
  font-size: 20px;
  font-family: 'Raleway', sans-serif;
}

.research-page-image{
  width: 100%;
  text-align: center;

}
.research-page-image img{
  width: 500px;
  border-radius: 20px;
  border: 2px solid rgb(226, 225, 225);
  box-shadow: 0 0 11px rgba(10,10,10,.2); 
  margin: 20px;
  
}
.tag-item{
  color: #012f4e;
}

.tag-topics{
  display: flex;
  text-align: center;
  margin: auto;
  flex-wrap: wrap;
  align-items: center;
}
.tagtopic-item{
  padding: 5px 10px;
  background-color: #f6f6f6;
  border: 1px solid rgb(206, 205, 205);
  border-radius: 15px;
  margin: 5px 5px 5px 0px;
  font-size: 14px;

  
}




/* Banner */
#head {
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.6) url(./assets/images/banner-1.png);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-blend-mode: overlay;
  color: white;
  /* margin-top: 100px; */
  z-index: 0;
}

.banner-content {
  align-items: center;
  text-align: center;
  justify-content: center;
  margin: 20px;

}

.banner-content h1 {
  color: var(--theme-blue);
  font-size: 74px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 900;
  text-align: center;
  font-family: "Open sans", Helvetica, Arial;
}

.banner-content h2 {
  color: #fff;
  font-size: 44px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 500;
  text-align: center;
  font-family: "Open sans", Helvetica, Arial;

}


#section-head {
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.6) url(./assets/images/banner-1.png);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-blend-mode: overlay;
  color: white;
  /* margin-top: 100px; */
  z-index: 0;
  margin-bottom: 40px;
}



.section-banner-content {
  align-items: center;
  text-align: center;
  justify-content: center;
  margin: 20px;

}

.section-banner-content h1 {
  color: white;
  font-size: 60px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 900;
  text-align: center;
  font-family: "Open sans", Helvetica, Arial;
}
.section-banner-content h2 {
  color: white;
  font-size: 50px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 900;
  text-align: center;
  font-family: "Open sans", Helvetica, Arial;
}








.fund-item {
