  font-family: 'Raleway', sans-serif;
  
}






/* News */
.news-table a {
  text-decoration: none;
  color: black;
}

.news-table a:hover {
  text-decoration: none;
  color: rgb(7, 47, 132);
}

.news-table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  margin: auto;
  width: 100%;
  height: 100%;
}

.news-table td, th {
  border: 1px solid #ddd;
  padding: 8px;

}

.news-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

/* .news-table tr:hover {background-color: #ddd;} */

.news-table th {
  padding: 8px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #d9d9d9;
  color: rgb(0, 0, 0);
}

.news-pagination{
  display: flex;
  text-align: right;
}
.pagination-section{
  text-align: right;
  margin-top: 20px;
}
.news-pagination li{
  list-style: none;
}
.news-pagination-btn{
  margin-right: 5px;
  padding: 10px 15px;
  text-align: right;
  border: none;
  background-color: var(--theme-blue);
  border-radius: 5px;

}
.news-pagination-btn:hover{
  cursor: pointer;
  background-color: rgb(1, 111, 189);
}



/* Image Slideshow */

.image-slideshow {
  /* height: 100vh; */
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  display: flex;
  margin: auto;
  display: block;

}

.image-slideshow img {
  vertical-align: middle;
  width: 90%;
  border-radius: 20px;
  /* box-shadow:  5px 0px 10px 0px rgb(48, 48, 48); */

}

.prev {
  cursor: pointer;
  width: auto;
  padding: 10px;
  font-weight: bold;
  font-size: 30px;
  user-select: none;
  -webkit-user-select: none;
  border: none;
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
}

.next {
  cursor: pointer;
  width: auto;
  padding: 10px;
  font-weight: bold;
  font-size: 30px;
  user-select: none;
  -webkit-user-select: none;
  border: none;
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
}

.prev:hover,
.next:hover {
  color: rgb(254, 118, 118);
}



